import { ContentBlockEntity } from "@rp/react/rpjson/types"
import { EnhancedEntity } from "@rp/react/rpjson/types"

const enhanceEntity = (item: ContentBlockEntity, id: number): EnhancedEntity => ({
  ...item,
  id,
  key: item.rp_entity_id,
  queryType: "entity" as const,
})

const enhanceEntities = (array: Array<ContentBlockEntity>) => array.map(enhanceEntity)

export { enhanceEntities }
